.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 150px;
  min-height: 36px;
  padding: 0.375rem 0.75rem;
  border-radius: 0.5rem;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  color: #fff;
  gap: 1rem;
  justify-content: space-between;
  min-width: 150px;
  width: 100%;
  gap: 1rem;
}
.btn-instagram {
  color: #fff;
  background: #e1306c;
}
.btn-instagram:hover {
  background: #ea417a;
}
.btn-instagram > span, .btn-instagram > path {
  color: #fff;
  fill: #fff;
}
.btn-whatsapp {
  color: #fff;
  background: var(--orange3);
}
.btn-whatsapp:hover {
  background: #05cc06;
}
.btn-whatsapp > span, .btn-whatsapp > path {
  color: #fff;
  fill: #fff;
}
.btn-facebook {
  color: #fff;
  background: #00af01;
}
.btn-facebook:hover {
  background: #44619b;
}
.btn-facebook > span, .btn-facebook > path {
  color: #fff;
  fill: #fff;
}
.btn-telegram {
  color: #fff;
  background: #0088cc;
}
.btn-telegram:hover {
  background: #22a5e6;
}
.btn-telegram > span, .btn-telegram > path {
  color: #fff;
  fill: #fff;
}
.btn path {
  fill: #fff;
}