@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 150px;
  min-height: 36px;
  padding: 0.375rem 0.75rem;
  border-radius: 0.5rem;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  color: #fff;
  gap: 1rem;
  position: relative;
}
.btn--fullWidth {
  width: 100%;
  min-width: 100%;
}
.btn-withIcon {
  justify-content: space-between;
  align-items: center;
}
.btn-small {
  font-size: 1rem;
}
.btn-medium {
  font-size: 1.1rem;
  padding: 0.7rem 0.9rem;
}
.btn-disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btnLoadingContainer {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btnLoadingLabel {
  opacity: 0;
}
.btn-loading {
  width: 20px;
  height: 20px;
  transform: translate(50%, -50%);
  border: 2px solid #FFF;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  animation: rotation 1s linear infinite;
}
.btn-green {
  color: #fff;
  background: var(--green500);
}
.btn-green:hover {
  background: var(--green600);
}
.btn-green > span, .btn-green > path {
  color: #fff;
  fill: #fff;
}
.btn-green--outline {
  background: transparent;
  color: var(--green500);
  border: 2px solid var(--green500);
}
.btn-green--outline:hover {
  color: var(--green600);
  border: 2px solid var(--green600);
  background: transparent;
}
.btn-green--outline > span, .btn-green--outline > path {
  color: var(--green500);
  fill: var(--green500);
}
.btn-green--transparent {
  background: transparent;
  border: none;
  color: var(--green500);
  fill: var(--green500);
}
.btn-green--transparent:hover {
  color: var(--green600);
  background: transparent;
}
