@keyframes click-wave {
  0% {
    opacity: 0.25;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}
.container {
  display: block;
  position: relative;
  cursor: pointer;
  font-size: 1rem;
  padding-left: 25px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.container .input-checkbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.container .input-checkbox:checked ~ .input-checkmark {
  background: var(--green500);
}
.container .input-checkbox:checked ~ .input-checkmark::after {
  display: block;
}
.container .input-checkmark::after {
  left: 50%;
  top: 50%;
  width: 7px;
  height: 12px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
  -ms-transform: translate(-50%, -50%) rotate(45deg);
  transform: translate(-50%, -50%) rotate(45deg);
}

.input-checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 1.3rem;
  width: 1.3rem;
  background-color: var(--gray200);
}
.input-checkmark::after {
  content: "";
  position: absolute;
  display: none;
}