.btn {
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  padding: 0.1rem;
}
.btn-green {
  background: transparent;
  border: none;
  color: var(--green800);
  fill: var(--green800);
}
.btn-green:hover {
  color: var(--green100);
  background: transparent;
}
.btn-green span, .btn-green path {
  color: var(--green800);
  fill: var(--green800);
}
.btn-green:hover {
  color: var(--green800);
  background-color: var(--green100);
}
.btn-orange {
  background: transparent;
  border: none;
  color: var(--orange1);
  fill: var(--orange1);
}
.btn-orange:hover {
  color: var(--orange6);
  background: transparent;
}
.btn-orange span, .btn-orange path {
  color: var(--orange1);
  fill: var(--orange1);
}
.btn-orange:hover {
  color: var(--orange1);
  background-color: var(--orange6);
}
.btn-red {
  background: transparent;
  border: none;
  color: var(--berry3);
  fill: var(--berry3);
}
.btn-red:hover {
  color: var(--berry8);
  background: transparent;
}
.btn-red span, .btn-red path {
  color: var(--berry3);
  fill: var(--berry3);
}
.btn-red:hover {
  color: var(--berry3);
  background-color: var(--berry8);
}
.btn-primary {
  background: transparent;
  border: none;
  color: var(--blue3);
  fill: var(--blue3);
}
.btn-primary:hover {
  color: var(--blue7);
  background: transparent;
}
.btn-primary span, .btn-primary path {
  color: var(--blue3);
  fill: var(--blue3);
}
.btn-primary:hover {
  color: var(--blue3);
  background-color: var(--blue7);
}
.btn-gray {
  background: transparent;
  border: none;
  color: var(--gray500);
  fill: var(--gray500);
}
.btn-gray:hover {
  color: var(--gray100);
  background: transparent;
}
.btn-gray span, .btn-gray path {
  color: var(--gray500);
  fill: var(--gray500);
}
.btn-gray:hover {
  color: var(--gray500);
  background-color: var(--gray100);
}
.btn-sm {
  width: 1.7rem;
  height: 1.7rem;
}
.btn-sm span, .btn-sm svg {
  width: 1.4rem;
  height: 1.4rem;
  font-size: 1.4rem;
}
.btn-lg {
  width: 2rem;
  height: 2rem;
}
.btn-lg span, .btn-lg svg {
  width: 1.6rem;
  height: 1.6rem;
  font-size: 1.6rem;
}